import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';
import {getProject} from './store/urls';

//plugins
import vDialog from 'v-dialogs';
import vPage from 'v-page';
//import vTableGrid from 'v-tablegrid';
import vUploader from 'v-uploader';

import {uploaderConfig} from './hooks';
import {github, gitee} from './count';


Vue.use(router);

Vue.use(vDialog);
Vue.use(vPage);
//Vue.use(vTableGrid);
Vue.use(vUploader, uploaderConfig);

let setPrjCode = (path, store) => {
    if(!path) return;
    let url = getProject(path);
    if(url) store.commit('changeProject', url.key);
};

let doCount = store => {
    if(store.state.site === 'gitee') gitee();
    else github();
};

new Vue({
    el: '#app',
    router,
    store,
    render: h => h(App),
    beforeMount(){
        let that = this;
        //init current project in first time
        setPrjCode(that.$router.currentRoute.path, that.$store);
        doCount(that.$store);
        window._hmt.push(['_trackPageview', '/#' + this.$router.currentRoute.path]);
        this.$router.beforeEach((to, from, next)=>{
            setPrjCode(to.path, that.$store);
            next();
        });
        this.$router.afterEach((to, from, next)=>{
            //doCount(that.$store);
            window._hmt.push(['_trackPageview', '/#' + to.fullPath]);
        });
    }
});


